import { Link } from 'react-router-dom';
import './regForm.scss';

type PopupState = {
    isPopupHidden: boolean,
    setPopupState: (state: boolean) => void;
  };

const RegForm = ({
  isPopupHidden, setPopupState,
}: PopupState) => (
  <form className="login-form">
    <div className="form-group">
      <input type="email" className="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="Login" />
    </div>
    <div className="form-group">
      <input type="password" className="form-control" id="exampleInputPassword1" placeholder="Password" />
    </div>
    <div className="reg-btns">
      <Link to="/admin-page">
        <button type="button" className="btn-submit" onClick={() => { setPopupState(true); }}>Login</button>
      </Link>
      <button type="button" className="btn-cancel" onClick={() => { setPopupState(true); }}>Cancel</button>
    </div>
  </form>
);

export default RegForm;
