/* eslint-disable react/no-array-index-key */
/* eslint-disable jsx-a11y/no-noninteractive-element-interactions */
/* eslint-disable jsx-a11y/click-events-have-key-events */
import { Link } from 'react-router-dom';
import './navbar.scss';
import * as FaIcons from 'react-icons/fa';
import * as AiIcons from 'react-icons/ai';
import React, { useState } from 'react';
import Switcher from '../switch/Switcher';
import { useTypedSelector } from '../../hooks/useTypedSelector';
import { useActions } from '../../hooks/useActions';
import RegPopup from '../regPopup/regPopup';
// import '../../../node_modules/bootstrap/dist/css/bootstrap.min.css';

type SidebarState = {
    isOpen: boolean,
    setSidebarState: (state: boolean) => void,
    isPopupHidden: boolean,
    setPopupState: (state: boolean) => void;
};

const Navbar = ({
  isOpen, setSidebarState,
  isPopupHidden, setPopupState,
}: SidebarState): JSX.Element => {
  const { categoriesList } = useTypedSelector((state) => state.categoriesState);
  const { isGameStarted } = useTypedSelector((state) => state.gameState);
  const { revertCardsState } = useActions();
  const showSidebar = () => setSidebarState(!isOpen);
  const { changeGameState } = useActions();
  const [btnClass, setBtnClass] = useState('nav-text');
  const changeState = () => {
    if (isGameStarted) {
      changeGameState();
      revertCardsState();
    }
  };
  return (
    <>
      <div className="header-navbar">
        <div className="menu-bars">
          <FaIcons.FaBars onClick={showSidebar} />
        </div>
        <Switcher />
        {/* <button>
                <Link to="/stats">
                Stats
                        </Link>
                    </button> */}
      </div>

      <nav className={isOpen ? 'nav-menu active' : 'nav-menu'} onClick={(e) => e.stopPropagation()}>
        <ul className="nav-menu-items" onClick={showSidebar}>
          <li key={100} className="navbar-toggle">
            <div className="menu-bars">
              <AiIcons.AiOutlineClose />
            </div>
          </li>
          <li key={101} className={btnClass} onClick={changeState}>
            <Link to="/">
              <span>Home</span>
            </Link>
          </li>
          {
                        categoriesList.map((category: {
                            categoryPath: string; categoryName: string; image:string; },
                        idx: number) => {
                          const { categoryName, categoryPath, image } = category;
                          return (
                            <li key={idx} className={btnClass} onClick={changeState}>
                              <Link to={categoryPath}>
                                <span>{categoryName}</span>
                              </Link>
                            </li>
                          );
                        })
                    }
        </ul>
        <button type="button" className="btn-login" onClick={() => { setPopupState(false); }}>Login</button>
      </nav>
    </>
  );
};

export default Navbar;
