/* eslint-disable no-plusplus */
import cards from '../../cards';
import { CardsAction, CardsActionTypes } from '../../types/cards';

export function changeCardsState(id: string): CardsAction {
  return { type: CardsActionTypes.CHANGE_CARDS_STATE, payload: id };
}

export function changeCardClicksState(id: string): CardsAction {
  return { type: CardsActionTypes.CHANGE_CARD_CLICKS, payload: id };
}

export function changeCardFailuresState(id: string): CardsAction {
  return { type: CardsActionTypes.CHANGE_FAILED_CLICKS, payload: id };
}

export function revertCardsState(): CardsAction {
  return { type: CardsActionTypes.REVERT_STATE };
}
