import cards from '../cards';
import { CardState } from './card';

export interface CardsState {
    cardsList: CardState[][],
}

export enum CardsActionTypes {
    CHANGE_CARDS_STATE = 'CHANGE_CARDS_STATE',
    CHANGE_CARD_CLICKS = 'CHANGE_CARD_CLICKS',
    CHANGE_FAILED_CLICKS = 'CHANGE_FAILED_CLICKS',
    REVERT_STATE = 'REVERT_STATE',
}

interface changeCardsStateAction {
    type: CardsActionTypes.CHANGE_CARDS_STATE,
    // payload: typeof cards,
    payload: string,
}

interface changeCardClicksAction {
    type: CardsActionTypes.CHANGE_CARD_CLICKS,
    payload: string,
}

interface changeFailedCardsAction {
    type: CardsActionTypes.CHANGE_FAILED_CLICKS,
    payload: string,
}

interface revertStateAction {
    type: CardsActionTypes.REVERT_STATE,
}

export type CardsAction = changeCardsStateAction | changeCardClicksAction |
changeFailedCardsAction |
revertStateAction;
