/* eslint-disable no-param-reassign */
/* eslint-disable no-unused-expressions */
import cards from '../../cards';
import { CardsAction, CardsActionTypes, CardsState } from '../../types/cards';

const initialState: CardsState = {
  cardsList: cards,
};

export const CardsReducer = (state = initialState, action: CardsAction): CardsState => {
  switch (action.type) {
    case CardsActionTypes.CHANGE_CARDS_STATE:
      state.cardsList.forEach((gameCards) => {
        gameCards.forEach((card) => {
          !card.isCardGuessed ? card.isCardGuessed = (card.word === action.payload) : '';
        });
      });
      return {
        ...state,
      };
    case CardsActionTypes.CHANGE_CARD_CLICKS:
      state.cardsList.forEach((gameCards) => {
        gameCards.forEach((card) => {
          card.word === action.payload ? (card.clicks += 1) : '';
        });
      });
      return {
        ...state,
      };
    case CardsActionTypes.CHANGE_FAILED_CLICKS:
      state.cardsList.forEach((gameCards) => {
        gameCards.forEach((card) => {
          card.word === action.payload ? (card.wrongClicks += 1) : '';
        });
      });
      return {
        ...state,
      };
    case CardsActionTypes.REVERT_STATE:
      state.cardsList.forEach((gameCards) => {
        gameCards.forEach((card) => {
          card.isCardGuessed = false;
        });
      });
      return {
        ...state,
      };
    default:
      return state;
  }
};
