import React, { useState } from 'react';
import { BsFillMicFill, BsArrowClockwise } from 'react-icons/bs';
import './card.scss';
import { useTypedSelector } from '../../../../hooks/useTypedSelector';
import { CardState } from '../../../../types/card';

type Card = {
  card: CardState,
  click: (word: string) => void,
};

const Card = ({ card, click }: Card): JSX.Element => {
  const {
    word, translation, image, audioSrc, isCardGuessed,
  } = card;
  const { mode } = useTypedSelector((state) => state.mode);
  const [flip, setFlip] = useState(false);
  const audio = new Audio(audioSrc);
  const cStyle = 'card__front game ';
  const start = () => {
    audio.play();
  };

  const revertCard = () => setFlip(!flip);

  const flipCard = () => (flip ? setFlip(!flip) : '');

  if (mode === 'train') {
    return (
      <div className={flip ? 'card-container flipped' : 'card-container'} onMouseLeave={flipCard}>
        <div className="card">
          <div role="button" className="card__front" tabIndex={0} onClick={start} onKeyDown={start}>
            <div className="img-wrapper">
              <img className="card-img" src={image} alt={word} />
            </div>
            <div className="card-desc">
              <div className="card-name">{word}</div>
              <div className="card-icons">
                <BsFillMicFill className="audio-icon" onClick={start} />
                <BsArrowClockwise className="revert-icon" onClick={revertCard} />
              </div>
            </div>
          </div>
          <div className="card__back">
            <div className="img-wrapper">
              <img className="card-img" src={image} alt={word} />
            </div>
            <div className="card-desc">
              <div className="card-name">{translation}</div>
            </div>
          </div>
        </div>
      </div>
    );
  }
  return (
    <div className={flip ? 'card-container flipped' : 'card-container'} onMouseLeave={flipCard}>
      <div role="button" className={isCardGuessed ? 'card hidden' : 'card'} tabIndex={0} onClick={() => click(word)} onKeyDown={() => click(word)}>
        <div className={cStyle}>
          <div className="img-wrapper">
            <img className="card-img" src={image} alt={word} />
          </div>
          <div className="card-desc hidden">
            <div className="card-name">{word}</div>
            <div className="card-icons">
              <BsFillMicFill className="audio-icon" onClick={start} />
              <BsArrowClockwise className="revert-icon" onClick={revertCard} />
            </div>
          </div>
        </div>
        <div className="card__back">
          <div className="img-wrapper">
            <img className="card-img" src={image} alt={word} />
          </div>
          <div className="card-desc">
            <div className="card-name">{translation}</div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Card;
