import RegForm from '../regForm/RegForm';
import './regPopup.scss';

type PopupState = {
  isPopupHidden: boolean,
  setPopupState: (state: boolean) => void;
};

const RegPopup = ({
  isPopupHidden, setPopupState,
}: PopupState) => (
  <div className={isPopupHidden ? 'popup hidden' : 'popup'}>
    <div className="modal-header">
      <h5 className="modal-title">Login</h5>
    </div>
    <RegForm
      isPopupHidden={isPopupHidden}
      setPopupState={setPopupState}
    />
  </div>
);

export default RegPopup;
