import { MovesAction, MovesActionTypes, MovesState } from '../../types/moves';

const initialState: MovesState = {
  movesNum: 0,
  failures: 0,
};

export const MovesReducer = (state = initialState, action: MovesAction): MovesState => {
  switch (action.type) {
    case MovesActionTypes.CHANGE_MOVES_NUMBER:
      return {
        ...state, movesNum: state.movesNum + 1,
      };
    case MovesActionTypes.CHANGE_FAILURES_NUMBER:
      return {
        ...state, failures: state.failures + 1,
      };
    default:
      return state;
  }
};
