import { ModeAction, ModeActionTypes, ModeState } from '../../types/mode';

const initialState: ModeState = {
  mode: 'train',
};

export const ModeReducer = (state = initialState, action: ModeAction): ModeState => {
  switch (action.type) {
    case ModeActionTypes.CHANGE_MODE:
      if (state.mode === 'train') {
        return { mode: 'game' };
      }
      return { mode: 'train' };
    default:
      return state;
  }
};
