import { GameAction, GameActionTypes } from '../../types/game';

export function changeGameState(): GameAction {
  return { type: GameActionTypes.CHANGE_GAME_STATE };
}

export function changeGameCards(newSet: string[]): GameAction {
  return { type: GameActionTypes.CHANGE_GAME_CARDS, payload: newSet };
}

export function changeActiveCard(newCard: string): GameAction {
  return { type: GameActionTypes.CHANGE_ACTIVE_CARD, payload: newCard };
}
