import {
  Link, Route, Router, Switch, useRouteMatch,
} from 'react-router-dom';
import CardsSection from '../cards-section/CardsSection';
import CategoriesSection from '../categories-section/CategoriesSection';
import './adminHeader.scss';

const AdminHeader = () => {
  const { path, url } = useRouteMatch();
  return (
    <div className="adminHeader">
      <div className="headerContent">
        <ul className="nav-items">
          <li>
            <Link to={`${url}/categories`}>Categories</Link>
          </li>
          <li>
            <Link to={`${url}/cards`}>Cards</Link>
          </li>
        </ul>

        <button className="logout-btn" type="button">
          <Link to="/">
            Logout
          </Link>
        </button>
      </div>

      <Switch>
        <Route exact path={path}>
          <CategoriesSection />
        </Route>
        <Route exact path={`${path}/categories`}>
          <CategoriesSection />
        </Route>
        <Route exact path={`${path}/cards`}>
          <CardsSection />
        </Route>
        <Route path={`${path}/:topicId`}>
          Topiiiic
        </Route>
      </Switch>
    </div>
  );
};

export default AdminHeader;
