import { GameAction, GameActionTypes, GameState } from '../../types/game';

const initialState: GameState = {
  isGameStarted: false,
  gameCards: [],
  activeCard: '',
};

export const GameReducer = (state = initialState, action: GameAction): GameState => {
  switch (action.type) {
    case GameActionTypes.CHANGE_GAME_STATE:
      return {
        ...state, isGameStarted: !state.isGameStarted,
      };
    case GameActionTypes.CHANGE_GAME_CARDS:
      return {
        ...state, gameCards: action.payload,
      };
    case GameActionTypes.CHANGE_ACTIVE_CARD:
      return {
        ...state, activeCard: action.payload,
      };
    default:
      return state;
  }
};
