/* eslint-disable max-len */
export interface GameState {
    isGameStarted: boolean,
    gameCards: string[],
    activeCard: string
}

export enum GameActionTypes {
    CHANGE_GAME_STATE = 'CHANGE_GAME_STATE',
    CHANGE_GAME_CARDS = 'CHANGE_GAME_CARDS',
    CHANGE_ACTIVE_CARD = 'CHANGE_ACTIVE_CARD',
}

interface changeGCardsAction {
    type: GameActionTypes.CHANGE_GAME_CARDS,
    payload: string[],
}

interface ChangeGameAction {
    type: GameActionTypes.CHANGE_GAME_STATE,
}

interface ChangeACardAction {
    type: GameActionTypes.CHANGE_ACTIVE_CARD,
    payload: string,
}

export type GameAction = ChangeGameAction | changeGCardsAction | ChangeACardAction
