/* eslint-disable jsx-a11y/no-static-element-interactions */
/* eslint-disable jsx-a11y/click-events-have-key-events */
import './App.css';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import { Provider } from 'react-redux';
import { useState } from 'react';
import { store } from './store';
import PageBuilder from './pages/pageBuilder/pageBuilder';
import RegPopup from './components/regPopup/regPopup';
import Navbar from './components/navbar/Navbar';
import AdminPage from './pages/adminPage/AdminPage';

function App() {
  const [isPopupHidden, setPopupState] = useState(true);
  const [isSidebarOpen, setSidebarState] = useState(false);

  const hideAll = () => {
    setPopupState(true);
    setSidebarState(false);
  };
  return (
    <Provider store={store}>
      <div className="App">
        <Router>
          {/* <Navbar
            isPopupHidden={isPopupHidden}
            setPopupState={setPopupState}
            isOpen={isSidebarOpen}
            setSidebarState={setSidebarState}
          /> */}
          <Switch>
            <Route
              path="/"
              exact
              component={() => (
                <PageBuilder
                  pageType="categories"
                  id={0}
                  isPopupHidden={isPopupHidden}
                  setPopupState={setPopupState}
                />
              )}
            />
            <Route
              path="/action1"
              component={() => (
                <PageBuilder
                  pageType="cards"
                  id={0}
                  isPopupHidden={isPopupHidden}
                  setPopupState={setPopupState}
                />
              )}
            />
            <Route
              path="/action2"
              component={() => (
                <PageBuilder
                  pageType="cards"
                  id={1}
                  isPopupHidden={isPopupHidden}
                  setPopupState={setPopupState}
                />
              )}
            />
            <Route
              path="/animal1"
              component={() => (
                <PageBuilder
                  pageType="cards"
                  id={2}
                  isPopupHidden={isPopupHidden}
                  setPopupState={setPopupState}
                />
              )}
            />
            <Route
              path="/animal2"
              component={() => (
                <PageBuilder
                  pageType="cards"
                  id={3}
                  isPopupHidden={isPopupHidden}
                  setPopupState={setPopupState}
                />
              )}
            />
            <Route
              path="/clothes"
              component={() => (
                <PageBuilder
                  pageType="cards"
                  id={4}
                  isPopupHidden={isPopupHidden}
                  setPopupState={setPopupState}
                />
              )}
            />
            <Route
              path="/emotions"
              component={() => (
                <PageBuilder
                  pageType="cards"
                  id={5}
                  isPopupHidden={isPopupHidden}
                  setPopupState={setPopupState}
                />
              )}
            />
            <Route
              path="/food"
              component={() => (
                <PageBuilder
                  pageType="cards"
                  id={6}
                  isPopupHidden={isPopupHidden}
                  setPopupState={setPopupState}
                />
              )}
            />
            <Route
              path="/places"
              component={() => (
                <PageBuilder
                  pageType="cards"
                  id={7}
                  isPopupHidden={isPopupHidden}
                  setPopupState={setPopupState}
                />
              )}
            />
            <Route
              path="/final"
              component={() => (
                <PageBuilder
                  pageType="final-page"
                  id={0}
                  isPopupHidden={isPopupHidden}
                  setPopupState={setPopupState}
                />
              )}
            />
            <Route
              path="/stats"
              component={() => (
                <PageBuilder
                  pageType="stats"
                  id={0}
                  isPopupHidden={isPopupHidden}
                  setPopupState={setPopupState}
                />
              )}
            />
            <Route path="/admin-page" component={() => <AdminPage />} />
          </Switch>
          <RegPopup
            isPopupHidden={isPopupHidden}
            setPopupState={setPopupState}
          />
        </Router>
      </div>
      <div className={isPopupHidden ? 'cover hidden' : 'cover'} onClick={hideAll} />
    </Provider>
  );
}

export default App;
