/* eslint-disable no-restricted-globals */
import { useEffect } from 'react';
import { useTypedSelector } from '../../hooks/useTypedSelector';

const FinalPage = () => {
  const { failures } = useTypedSelector((state) => state.movesState);

  useEffect(() => {
    setTimeout(() => {
      location.href = '/';
    }, 2000);
  });

  return (
    <div className="final-page">
      { failures > 0 ? (
        <div className="result-wrapper">
          <img src="img/failure.jpg" alt="Failure" />
          <p>
            You have done
            {' '}
            {failures}
            {' '}
            mistake(s)!
          </p>
        </div>
      ) : (
        <div className="result-wrapper">
          <img src="img/dancing.jpg" alt="Success" />
          <p>
            Congrats!
            You are the real winner today!
          </p>
        </div>
      )}
    </div>
  );
};

export default FinalPage;
